import { FC, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router';

import './Ingredient.css';

import { StockProduct } from '../../../../types/type';
import Details from './Details';

interface IngredientProps {
  stockProduct: StockProduct;
  nameOfMeal: string;
  mealCollection: any;
}

const Ingredient: FC<IngredientProps> = ({
  stockProduct,
  nameOfMeal,
  mealCollection,
}) => {
  const [showModal, setShowModal] = useState(false);
  const navigate = useNavigate()
  const { t } = useTranslation();


  const enterIngredient = () => {
    setShowModal(true);
    navigate('details')
  };

  return (
    <div>
      <div className="ingredient" onClick={enterIngredient}>
        <div className="ingredient-header">
          <p className="ingredient-title">
            {`${t(`key_ingredients.${stockProduct.title}`)} `}{' '}
          </p>
          <p className="ingredient-amount">{t('quantity_available')} - {stockProduct.amount}</p>
        </div>
      </div>
      {showModal ? (
        <Details
          stockProduct={stockProduct}
          showModal={showModal}
          setShowModal={setShowModal}
          nameOfMeal={nameOfMeal}
          mealCollection={mealCollection}
        />
      ) : null}
    </div>
  );
};

export default Ingredient;
