import { FC, useRef, useEffect, useState } from 'react';
import {
  addDoc,
  collection,
  doc,
  serverTimestamp,
  setDoc,
} from 'firebase/firestore';
import { db } from '../../../../firebase/config';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router';

import { StockProduct } from '../../../../types/type';

import '../../../../globalStyles/Details.css';
import close from '../../../../icons/close.svg';
import add_circle from '../../../../icons/add_circle_40.svg';

interface DetailsProps {
  stockProduct: StockProduct;
  showModal: boolean;
  setShowModal: (showModal: boolean) => void;
  nameOfMeal: string;
  mealCollection: any;
}

const Details: FC<DetailsProps> = ({
  stockProduct,
  showModal,
  setShowModal,
  nameOfMeal,
  mealCollection,
}) => {
  const [inputNumber, setInputNumber] = useState(0);
  const wrapperRef = useRef<HTMLDivElement>(null);
  const navigate = useNavigate();
  const { t } = useTranslation();

  const { title, amount, id, shoppingListAmount } = stockProduct;

  const modalStyle = showModal ? 'details-modal-container' : '';

  const closeMoreInformation = () => {
    setShowModal(false);
    navigate('.'); // Remove last part of path
  };

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (
        wrapperRef.current &&
        !wrapperRef.current.contains(event.target as Node)
      ) {
        setShowModal(false);
        navigate('.'); // Remove last part of path
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [setShowModal]);

  const addIngredientToMeal = async () => {
    const itemMealCollection = mealCollection.find(
      (findItem: any) => findItem.id === id
    );

    if (inputNumber > 0) {
      if (inputNumber <= amount) {
        if (itemMealCollection) {
          await setDoc(doc(db, `${nameOfMeal}`, id), {
            title,
            amount: itemMealCollection.amount + inputNumber,
            isEditing: false,
          });
        } else {
          await setDoc(doc(db, `${nameOfMeal}`, id), {
            title,
            amount: inputNumber,
            isEditing: false,
          });
        }
        await setDoc(doc(db, 'products', id), {
          title: title,
          amount: amount - inputNumber,
          shoppingListAmount: shoppingListAmount,
        });

        await addDoc(collection(db, 'historyOfCooking'), {
          title,
          amount: inputNumber,
          createdAt: serverTimestamp(),
          nameOfMeal: nameOfMeal,
        });

        setInputNumber(0);
        setShowModal(false);
      } else {
        alert(t('alert_not_enough'));
      }
    }
  };

  return (
    <div className={modalStyle}>
      <div ref={wrapperRef} className="details-inner">
        <div className="details-header">
          <div>
            <p>{t('quantity_available')}: {amount}</p>
            <img
              src={close}
              alt="close"
              className="close-img"
              onClick={closeMoreInformation}
            />
          </div>
          <h1>{t(`key_ingredients.${title}`)}</h1>
        </div>
        <div className="details-form">
          <input
            className="details-input"
            type="number"
            value={inputNumber}
            min={0}
            max={amount}
            onChange={(e) => setInputNumber(parseInt(e.target.value))}
            onFocus={(e: React.ChangeEvent<HTMLInputElement>) =>
              e.target.select()
            }
          />
          <button onClick={addIngredientToMeal}>
            {t('add')}
          </button>
        </div>
      </div>
    </div>
  );
};

export default Details;
