import { useEffect, FC } from 'react';
import { Link, useNavigate, Outlet, Routes, Route } from 'react-router-dom';

import './Meal.css';

import SearchBarMeal from './SearchBarMeal';
import {
  BackToCook,
  BackToMealList,
  BackToMealListFromEdit,
} from '../../consts/ArrowBack';

interface MealProps {
  title: string;
  backToCook: string;
  nameOfMeal: string;
  mealCollection: any;
}

const Meal: FC<MealProps> = ({
  title,
  backToCook,
  nameOfMeal,
  mealCollection,
}) => {
  const navigate = useNavigate();

  useEffect(() => {
    if (!mealCollection.length) {
      navigate('ingredients');
    }
  }, [mealCollection.length, navigate]);

  return (
    <div className="meal-container">
      <div className="inside-meal">
        <div className="meal-header">
          <Routes>
               {/* Arrow back When we are in MealList */}
            <Route path={backToCook} element={<BackToCook />} />
                {/*Arrow Back When we are in Ingredients */} 
            <Route                                  
              path="ingredients"
              element={
                <BackToMealList
                  nameOfMeal={nameOfMeal}
                  collection={mealCollection}
                />
              }
            />
             {/*Arrow Back When we are in Edit meal */}
            <Route                
              path="edit"
              element={<BackToMealListFromEdit nameOfMeal={nameOfMeal} />}     
            />     
          </Routes>
          <Link to={'/cook'} className="meal-title">
            <h2>{title}</h2>
          </Link>
        </div>
        {mealCollection.length &&
        localStorage.getItem('i18nextLng') === 'en' ? (
          <SearchBarMeal />
        ) : null}
        <div>
        <Outlet />     {/*Inside is MealList */}
        </div>
      </div>
    </div>
  );
};

export default Meal;
